import React from 'react'
import './projects.css'
import picFive from './photo5.jpg'
import picFour from './photo4.jpg'
import AR from './AR.png'
import fauni from './fauni.png'
import lucki from './lucki.png'

const yoSlowDown = {
  fontFamily: "Arial Black",
  fontSize: 15
}

const sizeIn = {
  height: 370,
  width: 400
}

const Home = () => {
    return(
    <body>
      <div className="content">
        <div className="content-inside">
        <div className="box projects">
           <h4>See You In Hell</h4>
           <p style = {yoSlowDown}>
             My name is Maika here on my website you'll find all the crazy shit that interest me plus my
             computer programming work. Wasn't really into school that much but rather 
             worked hard my whole life to learn on my own in order to expand my skills. Anyone who
             knows me knows I'm not much of a big talker, I mostly just chill and relax; However at any given random 
             moment the exciting part of me comes out and it's always a good time. I love music, I skate here and there,
             I collect knives :) , I game sometimes, I make art sometimes, I love hiking and camping , 
             I love animals, and I'm down for whatever. Hit me up in my contacts if you a real one. 
           </p>
        </div>
        <div className="box skills">
           <h4>Guitar Shit</h4>
           <img style= {sizeIn} src={picFive} alt="guitar"/>
           <p style = {yoSlowDown}>
             On April 25, 2020 I bought this electric 
             guitar at my local music shop.
             So far so good in terms of the sound 
             and quality of the guitar. It's
             a little dinged up and messy but 
             seeing that it's used its to be expected
             but overall its quality is really 
             good for a used guitar!
           </p>
           <img style= {sizeIn} src={picFour} alt="guitar"/>
        </div>
        <div className="box work">
          <h4>Miscellaneous</h4>
          <img style= {sizeIn} src={fauni} alt="fauni"/>
          <p style = {yoSlowDown}>
          lately I've been feeling really nostalgic when comes to my life and music.
          It's crazy how so much can change in just 5 years. Maika 2015-2018 should've 
          made a soundcloud to document the change in times when it came to some of my
          music. Anyways I say all that to say shout out to young figaro for 
          bringing me back with this song.
          </p>
          <img style= {sizeIn} src={lucki} alt="lucki"/>
          <p style = {yoSlowDown}>
           we're in 2020 and I'm just now getting more into the rapper Lucki. I've heard of him prior 
           to now of course (he did a song with FKA Twigs waaayyy back) but thats as much as I got to
           his music, from that point I only heard of him by name. But I don't know if it's my depression
           changing my mood or what but lately I've been getting more into his music and...I kinda like it.
          </p>
        </div>
        <div className="box programming">
          <h4>Augmented Reality Work</h4>
         <img style= {sizeIn} src={AR} alt="AR"/>
         <p style = {yoSlowDown}>
           May 7, 2020 I started to get more hands on my Augmented Reality project.
           I started to get familiar with Unity and Vuforia. After to some issues
           I was able to get everything working including my targets and so far so
           good. I've always been interested in Augmented Reality but never 
           thought I could do it. Hopefully this is the start to a beautiful new thing.
         </p>
      </div>
      </div>
      </div>
    </body>
    )
}

export default Home;